<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="mt5">
        <span class="mr25">月份</span>
        <el-date-picker
            format="yyyy年MM月"
            :clearable="false"
            v-model="form.salaryMonth"
            @change="getDetail"
            type="month"
            placeholder="选择时间">
        </el-date-picker>
        <span v-show="imported" class="ml20 gary">注：当月工资模板已生成，请勿轻易修改。修改工资模板后原工资模板则失效，需下载新模板方可成功导入工资信息。</span>
      </div>
      <div class="item-box mt20">
        <div class="item" v-for="item in data">
          <span class="bold">{{item}}</span>
        </div>
        <div v-for="(item,key) in form.templateList" class="item">
          <span v-show="!item.status" :title="item.name">{{item.name}}</span>
          <el-input v-show="item.status" v-model="itemName" placeholder="请输入" size="small"></el-input>

          <span v-show="item.status" class="icons">
            <i @click="editItemFun(key)" class="el-icon-check"></i>&nbsp;
            <i v-show="!item.name" @click="form.templateList.splice(key,1)" class="el-icon-close"></i>
            <i v-show="item.name" @click="item.status = false" class="el-icon-close"></i>
          </span>

          <span v-show="!item.status" class="icons">
            <i @click="showEditForm(item,key)" class="el-icon-edit-outline"></i>
            <i @click="form.templateList.splice(key,1)" class="el-icon-delete"></i>
            <i @click="swapItems(key,'left')" v-show="key !== 0" class="el-icon-caret-left"></i>
            <i @click="swapItems(key,'right')" v-show="key < form.templateList.length - 1" class="el-icon-caret-right"></i>
          </span>

        </div>
        <div class="item">
          <span class="bold">实发数</span>
        </div>
        <div @click="showItemForm" class="item tc">
          <i class="el-icon-plus"></i>
        </div>
      </div>
      <div class="btns">
        <div @click="doSubmit" class="btn-submit btn-save mr15">保存工资模板</div>
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "工资信息", path: "go(-1)"},{name: "设置工资模板", path: ""}],
        data:['基本工资', '其中：基本工资1', '其中：之前浮动4%', '其中：浮动5%', '其中：浮动10%', '其中：校龄', '其中：工作年限奖', '应发数'],
        form:{
          salaryMonth: new Date(),
          templateList: []
        },
        imported: false,
        itemName: ''
      }
    },
    methods: {
      // 展现增加工资项弹窗
      showItemForm(){
        if(this.form.templateList.every(e=> !e.status)){
          this.form.templateList.push({name: '', status: true})
          this.itemName = ''
        }
      },
      // 增加 修改 工资项
      editItemFun(key){
        if(this.itemName){
          this.form.templateList[key].status = false
          this.form.templateList[key].name = this.itemName
        }else{
          this.$message.warning('请输入名称')
        }
      },
      // 移动
      swapItems(key, type){
        if(type === 'left' && key > 0){
          this.form.templateList[key] = this.form.templateList.splice(key - 1, 1, this.form.templateList[key])[0];
        }
        if(type === 'right' && key !== this.form.templateList.length - 1){
          this.form.templateList[key] = this.form.templateList.splice(key + 1, 1, this.form.templateList[key])[0];
        }
      },
      // 查询工资模板
      async getDetail(){
        let salaryMonth = new Date(this.form.salaryMonth).$Format('yyyy-MM-dd hh:mm:ss')
        let templateList = await this.$ajax({
          url: '/salary/querySalaryTemplateList.do',
          el: '.item-box',
          query:{
            salaryMonth
          }
        })

        templateList.forEach(e=>{
          e.status = false
        })
        this.form.templateList = templateList
        // 查询该月是否已经导入过数据 提示用户谨慎修改模板
        let res = await this.$ajax({
          url: '/salary/getSalaryMonth.do',
          query:{
            salaryMonth
          }
        })
        this.imported = res.isImport === 'YES'
      },
      // 展现编辑表单
      showEditForm(item,key){
        this.form.templateList.forEach((e,i)=>{
          if(e.status){
            e.status = false
            this.form.templateList.splice(i,1, e)
          }
        })
        item.status = true;
        this.form.templateList.splice(key,1, item)
        this.itemName = item.name
      },
      // 保存模板
      async doSubmit(){
        let templateList = []
        this.form.templateList.forEach(e=>{
          templateList.push(e.name)
        })
        await this.$ajax({
          url: '/salary/updateSalaryTemplate.do',
          el: '.btn-save',
          noText: true,
          query:{
            salaryMonth: new Date(this.form.salaryMonth).$Format('yyyy-MM-dd hh:mm:ss'),
            templateList
          }
        })
        this.$remind('保存成功')
        this.getDetail()
      }
    },
    mounted(){
      this.getDetail()
    }
  }
</script>
