        @include btn-opacity-dark()
      .item
        position: relative
        color: $gray-dark
        width: 290px
        height: 46px
        line-height: 46px
        float: left
        padding: 0 30px 0 15px
        border-right: 1px solid $border
        border-bottom: 1px solid $border
        .bold
          color: $fc-def
        .el-input
          vertical-align: top
          width: 200px
        span:first-child
          display: inline-block
          width: 180px
          @extend %text-overflow
        .icons
          position: absolute
          right: 5px
          font-size: 16px
          i
            cursor: pointer
            margin: 0 2px
            &:hover
              color: $main-c
            &:active
              color: $gray-dark
